import React from 'react';
import classNames from 'classnames';
import './Input.css'

function Input({ className, placeholder, onChange, value, action }) {

  const keyPress = (e) => {
    if(e.key === 'Enter')
      action(e);
  }

  const onClick = (e) => {
    e.stopPropagation();
  }

  return (
    <input
      className={classNames('input', className)}
      placeholder={placeholder}
      onChange={onChange}
      onClick={onClick}
      onKeyPress={keyPress}
      value={value}
    />
  );
}

export default Input;
