import React, { useState, } from 'react';
import Todo from './utils/Todo';
import Saver from './utils/Saver';
import './App.css';

import List, { ListTodo } from './components/List';

function App() {
  const [data, setData] = useState(!Saver.load() ? Todo.todos : JSON.parse(Saver.load()));
  const [currentList, setList] = useState(null);

  const createList = (list) => {
    const newList = Todo.newList(Todo.rnd(), list.data, list.value, []);
    const newData = Todo.addList(data, newList);
    setData(newData);
    Saver.save(newData);
  };

  const createTodo = (name) => {
    const newTodo = Todo.addTodo(currentList, Todo.newTodo(Todo.rnd(), name.value, false, false));
    const newData = Todo.insertTodo(data, currentList.id, newTodo);
    setData(newData);
    setList(newTodo);
    Saver.save(newData);
  }

  const removeTodo = (id) => {
    const newList = Todo.removeTodo(currentList, id);
    const newData = Todo.replaceList(data, newList);
    setList(newList);
    setData(newData);
    Saver.save(newData);
  }

  const removeList = (id) => {
    const newData = Todo.removeList(data, id);
    if (currentList && currentList.id === id)
      setList(null);
    setData(newData);
    Saver.save(newData);
  };

  const editTodo = (id, name, done, important) => {
    const newList = Todo.editTodo(currentList, id, name, done, important);
    const newData = Todo.replaceList(data, newList);
    setList(newList);
    setData(newData);
    Saver.save(newData);
  };

  const selectList = (id) => {
    const newList = Todo.getList(data, id);
    setList(newList);
  };


  return (
    <div className="app">
      <List className={currentList ? 'todo--left' : ''} data={data} createList={createList} currentList={currentList} selectList={selectList} remove={removeList} />
      { currentList ? <ListTodo currentList={currentList.todos} createTodo={createTodo} editTodo={editTodo} removeTodo={removeTodo} /> : null}
    </div>
  );
}

export default App;
