import React, { useState } from 'react';
import classNames from 'classnames';
import Input from '../Input';
import ListItemControl from './ListItemControl';

function ListTodoItem({ id, name, done, important, editTodo, removeTodo }) {
  const [itemName, setItemName] = useState(name);
  const [edit, setEdit] = useState(false);

  const removeItem = (e) => {
    e.stopPropagation();
    removeTodo(id);
  };

  const switchImportant = (e) => {
    e.stopPropagation();
    editTodo(id, itemName, done, !important);
  };

  const switchDone = (e) => {
    e.stopPropagation();
    editTodo(id, itemName, !done, important);
  };

  const switchEdit = (e) => {
    e.stopPropagation();
    setEdit(!edit);
  }

  const editDone = (e) => {
    e.stopPropagation();
    if (itemName !== '') {
      editTodo(id, itemName, done, important);
      switchEdit(e);
    } else {
      alert(' ');
      setItemName(name);
    }
  }

  const onChange = (e) => {
    e.stopPropagation();
    setItemName(e.target.value);
  };

  return (
    <li onClick={switchDone} className={classNames({
      'list__item': true,
      'list__item--done': done,
      'list__item--important': important
    })}>
      {
        edit
          ? <Input className="list__input" value={itemName} onChange={onChange} action={editDone} />
          : <span className="list__name">{itemName}</span>
      }
      <div className="list__controls">
        {!done && !edit ? <ListItemControl type="edit" action={switchEdit} /> : null}
        {!done && !edit ? <ListItemControl type="important" action={switchImportant} /> : null}
        {!edit ? <ListItemControl type="remove" action={removeItem} /> : null}
        {edit ? <ListItemControl type="edit" action={editDone} /> : null}
      </div>
    </li>
  );
}

export default ListTodoItem;
